.header {
    margin-bottom: 20px;
}

.container {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
    justify-content: center;
    padding: 20px;
}

.card {
    background-color: #f5f5f5;
    border: 1px solid #ddd;
    border-radius: 8px;
    width: 300px;
    padding: 20px;
    box-shadow: 0 4px 6px rgba(0, 0, 0, 0.1);
    transition: transform 0.3s ease;
    text-align: left;
    /* Alinha o texto Ã  esquerda */
}

.card:hover {
    transform: translateY(-5px);
}

.image {
    width: 100%;
    height: 150px;
    object-fit: cover;
    border-radius: 4px;
}

.title {
    font-size: 1.5rem;
    margin: 10px 0;
    color: #333;
}

.description {
    font-size: 1rem;
    color: #666;
    margin-bottom: 15px;
}

.separator {
    border: none;
    border-top: 1px solid #ddd;
    margin: 15px 0;
}

.githubLink, .downloadButton {
    display: block;
    text-decoration: none;
    color: #e63946;
    font-weight: bold;
    text-align: left;
    font-size: 1rem;
    transition: color 0.3s ease;
}

.githubLink:hover, .downloadButton:hover {
    color: #b22222;
}
